.custom-select {
  .custom-select_container__control {
    transition: all ease 0.3s;
    border: 1px solid transparent;
    border-radius: 6px;
    box-shadow: 0px 14.3425px 47.8082px rgba(160, 163, 189, 0.3);
    z-index: 10;
    padding-top:.2rem;
    padding-bottom:.2rem;
    
.custom-select_container__placeholder{
  @apply text-gray-500 truncate
}
    &:hover {
      border-color: transparent;
    }
    // &:focus {
    //   border-color: #0086e1;
    // }
  }
  .custom-select_container__control--is-focused {
    border-color: #0086e1;

    &:hover {
      border-color: #0086e1;
    }
  }

  &.error {
    border: 1px solid #ef4444;
    border-radius: 6px;
    .custom-select_container__control--is-focused {
      border: 1px solid transparent;
    }
  }


  &.select--blue {
    .custom-select_container__control {
      background-color: #f3faff;
      box-shadow: none;
      z-index: 0;
    }
  }

  .custom-select_container__indicators {
    .custom-select_container__indicator-separator {
      display: none;
    }

    .custom-select_container__dropdown-indicator {
      color: #0086e1;
    }
  }

  .custom-select_container__menu {
    z-index: 12;
  }
}
